body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;

}
.container {
    /* background: linear-gradient(to left, #d3959b, #b993d6); */
    padding: 10px;

}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    /* background: #000; */
}

.logo img {
    width: 50px;
}

ul {
    list-style-type: none;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}

a {
    text-decoration: none;
    /* color: black; */
    transition: all 0.1s ease;
}

ul li {
    font-weight: 500;
}

ul li:hover {
    -webkit-transform: scale(1.2) !important;
            transform: scale(1.2) !important;
}

li a:hover {
    color: rgb(9, 114, 243);

}

.search-bar {
    /* background: rgb(250, 126, 126); */
    position: relative;
}

.search-bar input {
    border: none;
    outline: none;
    padding: 9px;
    background: transparent;
    /* border-radius: 10px; */
    /* color: #000; */
    text-indent: 20px;
    /* width: 160%; */
    font-size: 16px;
    border-bottom: 2px solid rgb(6, 4, 4);
}

.dark-search-bar input {
    border-bottom: 2px solid rgb(255, 255, 255);
    color: #fff;
}

.search-bar ::-webkit-input-placeholder {
    color: #000;
}

.search-bar :-ms-input-placeholder {
    color: #000;
}

.search-bar ::placeholder {
    color: #000;
}

.dark-search-bar ::-webkit-input-placeholder {
    color: #fff;
}

.dark-search-bar :-ms-input-placeholder {
    color: #fff;
}

.dark-search-bar ::placeholder {
    color: #fff;
}

.search-bar span {
    position: absolute;
    top: 11px;
    left: 9px;
}

.profiles img {
    width: 45px;
    object-fit: cover;
}

.user-img {
    width: 46px;
    height: 40px;
    border-radius: 50%;
    background: rgb(178, 14, 14) !important;
}


.active {
    border-bottom: 3px solid rgb(9, 114, 243);

}

/* .dark-class {
    background: #000 !important;
    color: aliceblue;
} */

.blackCol a {
    /* color: rgb(0, 0, 0) !important; */
    color: rgb(0, 0, 0) !important;
}

.whiteCol a {
    /* color: rgb(255, 255, 255) !important; */
    color: rgb(240, 240, 240) !important;
}



.show-moon {
    font-size: 25px !important;
    margin-top: 5px;
}

/* media query im mobile screen */

@media all and (max-width:500px) {



    .menus ul {
        /* display: block; */
        position: absolute;

        z-index: 2;

    }

    .menu-dropdown {
        width: 80%;
        height: 80vh;
        left: 2rem;
        text-align: center;
        border-radius: 3%;
        background: rgb(17, 164, 233);
        position: absolute;
        z-index: 10;
    }

    .menu-dropdown li {
        position: relative;
        top: 7rem;
        font-size: 1.3rem;
    }

    .no-menu-dropdown {
        display: none !important;
    }

    .menus li {
        margin-top: 20px !important;
    }

    .close-bar {
        position: relative;
        top: 3rem;
        right: 3rem;
        padding: 3px;
        z-index: 30;
        font-size: 30px;
    }

    .menu-dropdown {
        display: block !important;
    }

    .search-bar {
        margin: 0 10px 0 10px;
        /* margin: 10PX; */
        /* padding: 10px; */
    }

    .search-bar input {
        width: 90% !important;
        margin-left: .20rem;
        /* background: rgb(255, 255, 255); */
    }

    .show-moon {
        display: none;

    }

    .moon {
        background: rgb(30, 30, 30);
        color: aliceblue;
        font-size: 30px;
        /* z-index: 30; */
        /* display: block; */
        cursor: pointer;
        padding: 2px;
        border-radius: 40%;
    }

    .moon2 {
        background: rgb(246, 246, 246);
        color: #000;
        font-size: 30px;
        /* z-index: 30; */
        /* display: block; */
        cursor: pointer;
        padding: 2px;
        border-radius: 40%;
    }

    .moon-items {
        position: absolute;
        top: 5rem;
        right: 9rem;
        margin-bottom: 2rem;
        z-index: 30;
    }

    .movie-items span {
        cursor: pointer !important;
    }

    /* .moon-items {
   
        margin-bottom: 1rem;

    } */

    .nav-icon {
        /* width: 10px;
        height: 10px; */
        font-size: 30px;
        /* background: rgb(19, 241, 104); */
        cursor: pointer !important;

    }

    .profiles li:nth-child(2) {
        display: none !important;
    }

    /* .profiles li:nth-child(1) {
        display: block !important;
    } */
}

@media all and (min-width:350px) {

    .moon-items span {
        right: 11rem;
    }

    .close-bar {
        right: 4rem;
    }

}

@media all and (min-width:400px) {

